<template>
  <ion-page>
    <Back :btnSrc="() => router.push('/tabs/services')" />
    <Layout
      btnSrc="/tabs/servicesTechAllianceChoose"
      :method="storageHandler"
      height="false"
      filledBtn="Далее"
      outlineBtn="."
      title="Онлайн-заявка на технологическое подключение"
    >
      <template v-slot:header-content>
        <ion-text>
          <p class="text-20-600">Общие сведения заявления</p>
        </ion-text>
      </template>
      <template v-slot:main-content>
        <ion-text>
          <p class="title ion-text-start">Данные заявителя</p>
          <p>
            <ion-text class="dot">*</ion-text> - обязательное поле для
            заполнения.
          </p>
          <div v-for="(el, index) in formUser" :key="el">
            <div class="input-wrapper">
              <input
                ref="text"
                :type="el.type"
                class="input"
                placeholder=" "
                v-model="el.value"
              />
              <ion-text class="input-text" @click="onFocusText(index)"
                >{{ el.name
                }}<ion-text v-if="el.required" class="dot"
                  >*</ion-text
                ></ion-text
              >
            </div>
          </div>
        </ion-text>
      </template>
      <template v-slot:content>
        <LayoutBox>
          <template v-slot:content>
            <ion-text>
              <p class="title ion-text-start">Данные паспорта</p>
              <p>
                <ion-text class="dot">*</ion-text> - обязательное поле для
                заполнения.
              </p>
            </ion-text>

            <div v-for="(el, index) in formPass" :key="el">
              <div class="input-wrapper">
                <input
                  ref="text2"
                  :type="el.type"
                  class="input"
                  placeholder=" "
                  v-model="el.value"
                />
                <ion-text class="input-text" @click="onFocusText2(index)"
                  >{{ el.name
                  }}<ion-text v-if="el.required" class="dot"
                    >*</ion-text
                  ></ion-text
                >
              </div>
            </div>
          </template>
        </LayoutBox>
        <LayoutBox>
          <template v-slot:content>
            <ion-text>
              <p class="title ion-text-start">Данные об объекте газификации</p>
              <p>
                <ion-text class="dot">*</ion-text> - обязательное поле для
                заполнения.
              </p>
              <p class="sub-title">
                Наименование объекта капитального строительства
              </p>
            </ion-text>
            <div v-for="(el, index) in formGasName" :key="el">
              <ion-item class="check">
                <input
                  @click="
                    (e) => {
                      uniqueCheck(e);
                    }
                  "
                  :value="el.name"
                  v-model="gasHome"
                  @change="uniqueCheck"
                  class="check2"
                  type="checkbox"
                  slot="start"
                />
                {{ el.name }}
                <ion-text>{{ el }}{{ index }}</ion-text>
              </ion-item>
            </div>

            <ion-text>
              <p class="sub-title">
                Адрес обьекта <ion-text class="dot">*</ion-text>
              </p>
            </ion-text>
            <Input
              :value="address"
              :changeHandler="changeAddress"
              :name="'Введите данные '"
            />
            <ion-text>
              <p class="sub-title">
                Подключение в случаях (выбрать один из следующих вариантов)
                <ion-text class="dot">*</ion-text>
              </p>
            </ion-text>
            <ButtonSelect
              :btnSrc="
                () =>
                  router.push({
                    name: 'servicesTechAllianceSelect',
                    params: { connection: true },
                  })
              "
              :name="
                this.select?.filter(
                  (el) =>
                    el.GAS_SLUCHI?.NAME ===
                    'Подключение в случаях (выбрать один из следующих вариантов)'
                ).length !== 0
                  ? this.select?.filter(
                      (el) =>
                        el?.GAS_SLUCHI?.NAME ===
                        'Подключение в случаях (выбрать один из следующих вариантов)'
                    )[0]?.GAS_SLUCHI?.VALUE
                  : 'Выберите вариант'
              "
            />
            <ion-text>
              <p class="sub-title">
                Характер потребления газа
                <ion-text class="dot">*</ion-text>
              </p>
            </ion-text>
            <ButtonSelect
              :btnSrc="
                () =>
                  router.push({
                    name: 'servicesTechAllianceSelect',
                    params: { connection: false },
                  })
              "
              :name="
                this.select?.filter(
                  (el) => el?.GAS_HARAKTER?.NAME === 'Характер потребления газа'
                ).length !== 0
                  ? this.select?.filter(
                      (el) =>
                        el?.GAS_HARAKTER?.NAME === 'Характер потребления газа'
                    )[0]?.GAS_HARAKTER?.VALUE
                  : 'Выберите вариант'
              "
            />
            <ion-text>
              <p class="sub-title">
                Сроки проектирования, строительства и ввода в эксплуатацию
                объекта капитального строительства (в том числе по этапам и
                очередям)
                <ion-text class="dot">*</ion-text>
              </p>
            </ion-text>
            <Input
              :value="deadlines"
              :changeHandler="changeDeadlines"
              :name="'Введите данные'"
            />
          </template>
        </LayoutBox>
      </template>
    </Layout>
  </ion-page>
</template>

<script>
import { defineComponent } from "vue";
import { useRouter } from "vue-router";
import Layout from "../components/Layout.vue";
import { IonPage, IonText, IonItem, } from "@ionic/vue";
import Input from "../components/Input.vue";
import LayoutBox from "../components/LayoutBox.vue";
import Back from "../components/Back.vue";
import ButtonSelect from "../components/ButtonSelect.vue";
import { mapActions } from "pinia";
import { useServicesStore } from "../stores/services";
import { Storage } from "@ionic/storage";
export default defineComponent({
  name: "servicesTechAlliance",
  components: {
    IonPage,
    Layout,
    IonText,
    Input,
    Back,
    ButtonSelect,
    LayoutBox,
    IonItem,
  },
  computed: {
    formFields() {
      return this.$pinia.state.value?.services?.formResponse?.result?.forms.filter(
        (el) => {
          return (
            el.SERVICE.VALUE ===
            "Услуга технологического присоединения для физических лиц"
            /*  "Услуга технологического присоединения для физических лиц" */
          );
        }
      );
    },
    select() {
      return this.$pinia.state.value?.services?.select;
    },
  },
  methods: {
    ...mapActions(useServicesStore, ["getForms"]),
    async storageHandler() {
      const store = new Storage();
      await store.create();
      let formPass = {};
      for (let index = 0; index < this.$data.formPass.length; index++) {
        const element = this.$data.formPass[index];
        formPass[element.field] = { NAME: element.name, VALUE: element.value };
      }

      let formUser = {
        ...this.formFields[0],
        ...formPass,
        // ...this.$pinia.state.value?.services?.form[0],
      };
      for (let index = 0; index < this.$data.formUser.length; index++) {
        const element = this.$data.formUser[index];
        formUser[element.field] = { NAME: element.name, VALUE: element.value };
      }

      let userObject = {
        ...formUser,

        GAS_ADDRESS: { NAME: "Адрес объекта", VALUE: this.$data.address },
        GAS__SROK: {
          NAME: "Сроки проектирования, строительства и ввода в эксплуатацию объекта капитального строительства (в том числе по этапам и очередям)*",
          VALUE: this.$data.deadlines,
        },
        GAS_SLUCHI: {
          NAME: "Подключение в случаях (выбрать один из следующих вариантов)",
          VALUE: this.select[0]?.GAS_SLUCHI
            ? this.select[0]?.GAS_SLUCHI.VALUE
            : this.select[1]?.GAS_SLUCHI?.VALUE,
        },
        GAS_HARAKTER: {
          NAME: "Характер потребления газа",
          VALUE: this.select[0]?.GAS_HARAKTER
            ? this.select[0]?.GAS_HARAKTER?.VALUE
            : this.select[1]?.GAS_HARAKTER?.VALUE,
        },
        GAS_HOME: {
          NAME: "Наименование объекта капитального строительства",
          VALUE: this.$data.gasHome[0],
        },
      };
      if (this.$pinia.state.value?.services?.form) {
        this.$pinia.state.value?.services?.form?.push(userObject);
      }
      // let test = JSON.parse(this.$pinia.state.value?.services?.form)
      console.log("tessss", userObject, this.select);
      await store.set("servicesTechAlliance", JSON.stringify(userObject));
    },
    async uniqueCheck(e) {
      console.log(e.currentTarget.value, "test");
      this.$data.gasHome = [];
      // e.currentTarget.checked = false;
      if (e.currentTarget.checked === false) {
        /*  let data = this.$data.formGasName.filter((el)=>{
          el?.checked === true
        }) */

        this.$data.gasHome.push(e.currentTarget.value);
      }
    },
    onFocusText(index) {
      this.$refs.text[index].focus();
    },
    onFocusText2(index) {
      this.$refs.text2[index].focus();
    },
    changeDeadlines(e) {
      this.$data.deadlines = e.target.value;
    },
    changeAddress(e) {
      this.$data.address = e.target.value;
    },
    updateCheck(e) {
      console.log(e.currentTarget.checked, e, "test");
      // this.$data.address = e.target.value;
    },
  },
  mounted() {
    this.getForms();
    const fetchStoreHandler = async () => {
      const store = new Storage();
      await store.create();
      const servicesTechAlliance = await store.get("servicesTechAlliance");
      console.log(JSON.parse(servicesTechAlliance), "servicesTechAlliance");
    };
    fetchStoreHandler();
    console.log("test", this.formFields);
  },
  data() {
    return {
      formGasName: [
        {
          name: "Жилой дом",
        },
        {
          name: "Гараж",
        },
        {
          name: "Баня",
        },
        {
          name: "Другое",
        },
      ],
      address: "",
      deadlines: "",
      gasHome: [],
      formPass: [
        {
          field: "USER_PASSPORT_SERIAL",
          name: "Серия паспорта",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_PASSPORT_NUM",
          type: "text",
          name: "Номер паспорта",

          required: true,
          value: "",
        },
        {
          field: "USER_PASS_WHO",

          name: "Кем выдан",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_PASS_DATE",

          name: "Дата выдачи паспорта",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_REGION",

          name: "Место регистрации",
          type: "text",
          required: true,
          value: "",
        },
      ],
      formUser: [
        {
          field: "USER_NAME",
          name: "Имя",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_LAST_NAME",

          name: "Фамилия",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_SECOND_NAME",

          name: "Отчество",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_BIRTHDATE",

          name: "Дата рождения",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_BIRTHPLACE",

          name: "Место рождения",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_ADDRESS",

          name: "Место жительства",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_PHONE",

          name: "Контактный телефон",
          type: "text",
          required: true,
          value: "",
        },
        {
          field: "USER_PHONE_2",

          name: "Дополнительный контактный номер",
          type: "text",
          required: false,
          value: "",
        },
        {
          field: "USER_EMAIL",

          name: "E-mail",
          type: "text",
          required: true,
          value: "",
        },
      ],
    };
  },
  setup() {
    const router = useRouter();
    return { router };
  },
});
</script>

<style scoped>
.text-white {
  color: #fff;
}
.dot {
  color: #62d0ce;
}

.input {
  border-radius: 8px;
  padding: 15px;
  width: 100%;
  border: solid 1px #e0e0e0;
  margin-bottom: 15px;
  --padding-start: 15px;
  --padding-bottom: 14px;
  --padding-top: 14px;
  --placeholder-color: #9e9e9e;
  --placeholder-font-weight: 400;
}

.input:focus {
  outline: none !important;
  border: solid 1px #62d0ce;
  caret-color: #000;
}
.input-wrapper {
  position: relative;
  width: 100%;
}
.input-text {
  z-index: 0;
  padding-left: 15px;
  position: absolute;
  left: 1px;
  top: 15px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
}
.inputTextBlue {
  color: #0378b4;
  font-weight: 700;
}

input:not(:placeholder-shown) + ion-text {
  display: none;
}

.dot {
  color: #62d0ce;
}
</style>
